<template>
  <div class="crypto">
    <div class="custom-file">
      <label>
        <input
          @change="uploadFile"
          ref="customFile"
          type="file"
          :class="[isDragOver ? 'over' : '', 'custom-file-input']"
          @dragenter="isDragOver = true"
          @dragleave="isDragOver = false"
          @drop="isDragOver = false"
        />
        <div class="file-placeholder">
          <span class="file-name">{{ fileInfo }}</span>
          <span class="file-size" v-show="fileSize">{{ fileSize }}</span>
        </div>
      </label>
    </div>
    <el-input class="input-key" placeholder="请输入解密密码" v-model="dKey">
      <el-tooltip
        slot="append"
        effect="dark"
        content="自动生成一个安全密钥"
        placement="left"
      >
        <el-button @click="refreshKey" icon="el-icon-refresh"></el-button>
      </el-tooltip>
    </el-input>
    <div class="key-check">
      <p>密码强度：{{ percentageText }}</p>
      <el-progress
        :percentage="percentage"
        :stroke-width="10"
        :show-text="false"
        status="warning"
        stroke-linecap="square"
      ></el-progress>
    </div>
    <div class="operate">
      <el-button @click="encryptFile" type="primary" plain>加密</el-button>
      <el-button @click="decryptFile" type="success" plain>解密</el-button>
      <el-button @click="resetForm" plain v-if="resetShow" type="danger"
        >重置</el-button
      >
    </div>
    <div class="dangerInfo">
      <ul>
        <li>
          基于浏览器<span>AES-256-GCM</span>算法进行加密。快速、安全、离线。
        </li>
        <li>
          为防止因混淆而丢失密码,密码会在加密的下载文件名
          <span>#</span>
          之间。
        </li>
        <li>
          请务必<span>保存好您的密码</span>，一旦丢失，您将无法解密您的文件。
        </li>
        <li>文件大小理论不限制，但文件过大，浏览器可能无法读取。</li>
        <li><span>为保证唯一性,本网站不能和其他文件加密网站混用。</span></li>
      </ul>
    </div>
    <div class="result">
      <div v-for="(n, i) in resultList" :key="i">
        <resultEncryption
          v-if="n.type === 1"
          :dKey="n.dKey"
          :nameStr="n.nameStr"
          :blobStr="n.blobStr"
        />
        <resultDecryption
          v-show="n.type === 2"
          :nameStr="n.nameStr"
          :blobStr="n.blobStr"
        />
      </div>
    </div>
  </div>
</template>

<script>
import zxcvbn from "zxcvbn";
import resultEncryption from "./result-encryption";
import resultDecryption from "./result-decryption";
import { str2ab, generateKey } from "@/utils";
import { DEC } from "@/config";
export default {
  name: "Crypto",
  components: { resultEncryption, resultDecryption },
  data() {
    return {
      isDragOver: false,
      isHaveFile: false,
      file: null,
      fileInfo: "点击选择或拖入文件进行加密/解密",
      fileSize: null,
      dKey: "",
      percentage: 0,
      percentageText: "",
      resultList: [],
      loading: null,
    };
  },
  computed: {
    resetShow() {
      return this.isHaveFile || this.dKey;
    },
  },
  methods: {
    addEnterClass() {
      this.isDragOver = true;
    },
    dragleave() {},
    uploadFile(e) {
      const file = e.target.files[0];
      if (!file) {
        this.isHaveFile = false;
        this.fileInfo = "选择文件进行加密/解密";
        this.fileSize = "";
        return false;
      }
      this.file = file;
      this.isHaveFile = true;
      this.fileInfo = file.name;
      let nBytes = file.size;
      let fileSize = "";
      for (
        let aMultiples = ["KB", "MB", "GB", "TB", "PB", "EB", "ZB", "YB"],
          nMultiple = 0,
          nApprox = nBytes / 1024;
        nApprox > 1;
        nApprox /= 1024, nMultiple++
      ) {
        fileSize = nApprox.toFixed(2) + " " + aMultiples[nMultiple];
      }
      this.fileSize = fileSize || "";

      // 判断文件名是否是解密的
      let reg = /Encrypted#[^ \f\n\r\t\v#]*#/;
      if (this.fileInfo.match(reg)) {
        this.dKey = this.fileInfo
          .match(reg)[0]
          .replace("Encrypted#", "")
          .replace("#", "");
      } else {
        this.dKey = "";
      }
    },
    keyCheckMeter(val) {
      val = val.replace(/#/g, "");
      this.dKey = val;

      let strength = {
        1: "非常糟糕",
        2: "糟糕",
        3: "一般",
        4: "强",
        5: "非常强",
      };
      if (val) {
        let result = zxcvbn(val);
        const score = result.score + 1;
        this.percentage = score * 20;
        this.percentageText = strength[score];
      } else {
        this.percentage = 0;
        this.percentageText = "";
      }
    },
    refreshKey() {
      const dKey = generateKey();
      this.dKey = dKey;
    },
    resetForm() {
      this.dKey = "";
      this.$refs.customFile.value = "";
      this.file = null;
      this.fileInfo = "选择文件进行加密/解密";
      this.fileSize = "";
      this.percentageText = "";
    },
    importSecretKey(password) {
      let rawPassword = str2ab(password);
      return window.crypto.subtle.importKey(
        "raw",
        rawPassword,
        {
          name: DEC.algoName1,
        },
        false,
        DEC.perms1
      );
    },
    async deriveEncryptionSecretKey() {
      let getSecretKey = await this.importSecretKey(this.dKey);
      return window.crypto.subtle.deriveKey(
        {
          name: DEC.algoName1,
          salt: DEC.salt,
          iterations: DEC.itr,
          hash: {
            name: DEC.hash,
          },
        },
        getSecretKey,
        {
          name: DEC.algoName2,
          length: DEC.algoLength,
        },
        false,
        DEC.perms2
      );
    },
    processFinished(nameStr, data, type, dKey) {
      const blob = new Blob(data, {
        type: "application/octet-stream",
      });
      const blobStr = URL.createObjectURL(blob);
      this.resultList.unshift({ nameStr, type, dKey, blobStr });
    },
    async encryptFile() {
      const that = this;

      if (!that.file || !that.dKey) {
        that.$message.error("请上传文件或输入密码");
      } else {
        const derivedKey = await that.deriveEncryptionSecretKey();
        const fr = new FileReader();
        fr.onloadstart = async () => {
          that.loading = this.$loading({
            lock: true,
            text: "Loading",
            spinner: "el-icon-loading",
            background: "rgba(0, 0, 0, 0.7)",
          });
        };
        fr.onload = async () => {
          const iv = window.crypto.getRandomValues(new Uint8Array(16));
          const content = new Uint8Array(fr.result);
          await window.crypto.subtle
            .encrypt(
              {
                iv,
                name: DEC.algoName2,
              },
              derivedKey,
              content
            )
            .then(function (encrypted) {
              that.processFinished(
                "Encrypted#" + that.dKey + "#" + that.file.name,
                [DEC.signature, iv, DEC.salt, new Uint8Array(encrypted)],
                1,
                that.dKey
              );
              that.loading.close();
              that.resetForm();
            })
            .catch(function () {
              that.$message.error("加密失败，请稍后再试！");
            });
        };
        fr.onerror = () => {
          that.$message.error("文件读取失败，可能由于文件过大");
        };
        fr.readAsArrayBuffer(this.file);
      }
    },
    async decryptFile() {
      const that = this;
      if (!that.file || !that.dKey) {
        that.$message.error("请上传文件或输入密码");
      } else {
        const fr = new FileReader();
        fr.onloadstart = () => {
          that.loading = this.$loading({
            lock: true,
            text: "Loading",
            spinner: "el-icon-loading",
            background: "rgba(0, 0, 0, 0.7)",
          });
        };

        fr.onload = async () => {
          const deriveDecryptionSecretKey = async () => {
            let getSecretKey = await that.importSecretKey(that.dKey);

            return window.crypto.subtle.deriveKey(
              {
                name: DEC.algoName1,
                salt: new Uint8Array(
                  fr.result.slice(
                    DEC.signature.length + 16,
                    DEC.signature.length + 32
                  )
                ),
                iterations: DEC.itr,
                hash: {
                  name: DEC.hash,
                },
              },
              getSecretKey,
              {
                name: DEC.algoName2,
                length: DEC.algoLength,
              },
              false,
              DEC.perms2
            );
          };

          const derivedKey = await deriveDecryptionSecretKey();

          const iv = new Uint8Array(
            fr.result.slice(DEC.signature.length, DEC.signature.length + 16)
          );
          const content = new Uint8Array(
            fr.result.slice(DEC.signature.length + 32)
          );
          await window.crypto.subtle
            .decrypt(
              {
                iv,
                name: DEC.algoName2,
              },
              derivedKey,
              content
            )
            .then(function (decrypted) {
              // 判断文件名是否是解密的
              let reg = /Encrypted#[^ \f\n\r\t\v#]*#/;
              let FILE_NAME = that.file.name;
              if (FILE_NAME.match(reg)) {
                FILE_NAME = FILE_NAME.replace(FILE_NAME.match(reg)[0], "");
              }
              that.processFinished(
                FILE_NAME,
                [new Uint8Array(decrypted)],
                2,
                that.dKey
              );
              that.loading.close();
              that.resetForm();
            })
            .catch(function () {
              that.$message.error("您的解密密钥是错误的!");
              that.loading.close();
            });
        };
        fr.onerror = () => {
          that.$message.error("文件读取失败，可能由于文件过大");
        };
        fr.readAsArrayBuffer(that.file);
      }
    },
  },
  mounted() {},
  watch: {
    dKey: "keyCheckMeter",
  },
};
</script>

